export default "\nexport const setupProgressBar = (emblaApi, progressNode) => {\n    const applyProgress = () => {\n        const progress = Math.max(0, Math.min(1, emblaApi.scrollProgress()));\n        progressNode.style.transform = `translate3d(${progress * 100}%,0px,0px)`;\n    };\n    \n    return applyProgress;\n};\n";